import React, { useEffect, useState, useRef} from 'react'
import { mainSearch } from '../callers/search';
import { onEnter } from '../hooks';
import { changeParam, getParameter } from '../urlManager';
import ArtistRowList from './artist/ArtistRowList';
import { MovieRowList } from './movie/MovieRowList';
import UserRowList from './profile/UserRowList';
import { SeriesRowList } from './series/SeriesRowList';

export default function Search() {
    const [results, setResults] = useState({movies: [], series: [], artists: [], users: []});
    const [keyword, setKeyword] = useState(getParameter("keyword"));
    const keywordInput = useRef<HTMLInputElement>();
    useEffect(()=>{
        fetchResults();
        changeParam({name: "keyword", value: keyword});
    }, [keyword]);

    useEffect(()=>{
        keywordInput.current.value = keyword;
    }, []);

    async function fetchResults(){
        let result = await mainSearch(keyword, 40);
        if(result){
            setResults(result);
        }
    }

    async function searchBtnCommand(){
        setKeyword(keywordInput.current.value);
    }
    
    return (
        <div>
            <input ref={keywordInput} onKeyDown={(e)=> { onEnter(e, searchBtnCommand) }} />
            <button onClick={()=>{ searchBtnCommand() }}>Search</button>
            <MovieRowList movies={results.movies} />
            <SeriesRowList series={results.series} />
            <ArtistRowList artists={results.artists} />
            <UserRowList users={results.users} />
        </div>
    )
}
