import React, { Fragment } from 'react'
import { Link } from "react-router-dom"
import { IMovie } from '../../../common/movie';
import { getDay, getMonth, getYear } from '../../dateParser'
import MainScore from './MainScore'
import "./movieStyle.css";

interface IMovieInfo{
    movie: IMovie;
}

export const MovieInfo: React.FC<IMovieInfo> = ({movie}) => {
    return (
        <div>
            <div className="title-grid">
                <p className="title">{movie.title}</p>
                <MainScore value={movie.avgScore} />
            </div>
            <div>
            { parseInt(localStorage.getItem("permission")) > 1 ?
                    (<Link to={`/editmovie?movieId=${movie.id}`} >Edit this movie's page</Link>) : 
                    (<Fragment></Fragment>)
                    }
            </div>                     
            {(movie.genres != null)?
            (movie.genres.map(g=> {
                return <Link key={`genre-${g.id}`} className="genre" to={"todo"}>{g.genreName}</Link>
            })):
            (<Fragment></Fragment>)
            }
        
            <div className="movie-info-grid">               
                <div className="movie-info-grid-item">
                    <p>{movie.summary}</p>
                    <p>Premier: {`${getDay(movie.releaseDate.toString())} ${getMonth(movie.releaseDate.toString())}  ${getYear(movie.releaseDate.toString())}`}</p>
                    <p>Runtime: {`${movie.runtime} minutes`}</p>
                    <p>Directed by: </p>
                    <ul>
                        {movie.casts.filter(c=> c.castTypeId === 1).map(m=>{
                            return <li key={`director-link-${m.artistId}`} ><Link className="star" to={`/star?artistId=${m.artistId}`} title={m.name} >{m.artistName}</Link></li>
                        })}
                    </ul>
                    <p>Written by: </p>
                    <ul>
                        {movie.casts.filter(c=> c.castTypeId === 3).map(m=> {
                            return <li key={`director-link-${m.artistId}`}><Link className="star" to={`/star?artistId=${m.artistId}`} title={m.name} >{m.artistName}</Link><br/></li>
                        })} 
                    </ul>                        
                </div>
                {window.screen.width > 800 ?
                (<img src={`/rewer/uploads/posters/${movie.id}.jpg`} className="poster" title={movie.title} alt={`${movie.title}-poster`} />):
                (<Fragment></Fragment>)}
                   
            </div>
            
        </div>
    )
}
export default MovieInfo;
