import React, { useState, useEffect, Fragment } from 'react'
import { selectFollowedUsers } from '../../callers/userCaller'
import FollowedUserRow from './FollowedUserRow';

interface IFollowedUsers{
    //users: User[];
}

export const FollowedUsers: React.FC<IFollowedUsers> = () => {
    const [users, setUsers] = useState([]);
    useEffect(()=>{
        if(localStorage.getItem("userName")){
            getFollowedUsers();
        }
    }, []);

    async function getFollowedUsers(){
        let followed = await selectFollowedUsers({userName: localStorage.getItem("userName")});
        if(followed){
            setUsers(followed);
        }
    }

    return (
        <Fragment>
            <h3 className="center-text">Followed</h3>
            {users.map(u=>{
                return <FollowedUserRow key={`followed-${u.userName}`} user={u} />
            })}
        </Fragment>
    )
}
export default FollowedUsers;