import React, { Fragment, useEffect, useState } from "react";
import "../main.css";
import EditMovieDetails from "../components/movie/EditMovieDetails";
import { selectDetailedMovie, updateMovie, insertMovie, insertMovieGenre, deleteMovieGenre } from "../callers/movieCaller";
import { useBinder } from "../hooks";
import { changeParam, getParameter } from "../urlManager";
import { EditCast, } from "../components/movie/EditCast";
import GenreAdder from "../components/movie/GenreAdder";
import { IGenre, IMovieGenre } from "../../common/genre";

export default function EditMovie(){
    const [movie, setMovie, bindMovie] = useBinder({id: parseInt(getParameter("movieId")), title: "", releaseDate: new Date(), summary: "", runtime: 0, casts: [], genres: []});
    const [mainMsg, setMainMsg] = useState("");
    useEffect(()=>{
        if(movie.id > 0){
            getMovie();
        }
    }, []);
    useEffect(()=>{
        setMainMsg("");
    }, [movie.id]);
    
    async function getMovie(){
        let selectedMovie = await selectDetailedMovie(movie.id);
        if(selectedMovie){
            setMovie(selectedMovie);
        } else {
            alert("Server error");
        }
    }

    async function saveMovie(){
        if(await updateMovie(movie)){
            setMainMsg(`Movie details saved: ${new Date().getHours()} : ${new Date().getMinutes()}`)
        } else {
            setMainMsg("Internal error: movie details could not be saved.");
        }
    }

    async function createMovie(){
        const newId = await insertMovie(movie);
        if(newId){
            setMovie({...movie, id: newId});
            changeParam({name: "movieId", value: newId.toString()});
        } else {
            setMainMsg("Internal error: movie could not be created.");
        }
    }

    async function addGenre(genre: IGenre){
        let newMovieGenre: IMovieGenre = { 
            id: 0,
            movieId: movie.id, 
            genreId: genre.id, 
            genreName: genre.name };
        newMovieGenre.id = await insertMovieGenre(newMovieGenre);
        if(newMovieGenre.id > 0){
            setMovie({...movie, genres: [...movie.genres, newMovieGenre]});
        } else {
            alert("Server error: could not add genre.");
        }
    }

    async function removeGenre(id: number){
        if(await deleteMovieGenre(id)){
            setMovie({...movie, genres: movie.genres.filter(g=> g.id !== id)})
        } else {
            alert("Server error: could not delete genre.");
        }
    }
 
    return (
    <Fragment>
        <div className="main-container">
            {(movie.id > 0)?
            (<button onClick={saveMovie}>Save</button>) :
            (<button onClick={createMovie}>Create</button>)}
            {mainMsg}
            <EditMovieDetails movie={movie} setMovie={setMovie} movieBinder={bindMovie} />
            {(movie.id > 0)?
            (<Fragment>
                <GenreAdder movie={movie} onAdd={(genre: IGenre)=> { addGenre(genre) }} onDelete={(id:number)=> { removeGenre(id) }} />
                <EditCast movie={movie} setMovie={setMovie} />
            </Fragment>):
            (<Fragment></Fragment>)}
        </div>
    </Fragment>)
}