import React, { useState, useEffect, Fragment } from 'react'
import { ReviewRow } from './ReviewRow'
import { Link } from "react-router-dom"
import { IUserReview } from '../../../common/review';

interface IUserReviewRow{
    review: IUserReview;
}

export const UserReviewRow: React.FC<IUserReviewRow> = ({review}: IUserReviewRow)=> {
    const [imgsrc, setImgsrc] = useState(``);
    useEffect(()=>{
        if(review.seriesId != null){
            setImgsrc(`/rewer/uploads/seriesPosters/${review.seriesId}.jpg`);
        } else {
            setImgsrc(`/rewer/uploads/posters/${review.movieId}.jpg`);
        }
    }, [review]); 
    
    return (
        <div className="followed-review-grid">
            <div className="review-poster-container">
                
                    {(review.seriesId != null)?
                    (<Fragment><Link  to={`/series?seriesId=${review.seriesId}`} >
                        <img src={imgsrc} alt={`${review.movieTitle}-poster`} onError={()=> { setImgsrc(`/rewer/uploads/seriesPosters/0.jpg`)}} />
                        <p>{review.seriesTitle}</p>
                        </Link>
                        <Link to={`/movie?movieId=${review.movieId}`}><p>{review.movieTitle}</p></Link>
                    </Fragment>
                    ):
                    (<Link to={`/movie?movieId=${review.movieId}`} >
                        <img src={imgsrc} alt={`${review.movieTitle}-poster`} onError={()=> { setImgsrc(`/rewer/uploads/posters/0.jpg`)}} />
                        <p>{review.movieTitle}</p>
                    </Link>)}
            </div>
            <div>
                <ReviewRow review={review} />
            </div>
        </div>
    )
}

export default UserReviewRow;