import React, { Fragment, useState, useEffect } from 'react'
import { normalFormat } from '../../dateParser'
import { Link } from "react-router-dom"
import { IDetailedArtist } from '../../../common/artist';

interface IStarInfo{
    artist: IDetailedArtist;
}
//ebbe mr annyiszor nyltam bele, hogy vgl sikerlt teljesen kvethetetlenn tenni
export const StarInfo: React.FC<IStarInfo> = ({artist}: IStarInfo) => {
    const [died, setDied] = useState(null);
    const deathInfo = 
    <Fragment>
        <p>Died: <i>{`${normalFormat(artist.deathDate.toString())},`}</i></p>
        <p><i>{artist.deathPlace}</i></p>
    </Fragment>;

    const [bio, setBio] = useState(<p></p>);
    const briefBio = <Fragment><p>{`${artist.bio.substring(0, 1500)}...`}</p><p onClick={showAll} className="show-more">Show more</p></Fragment>;
    useEffect(()=>{
        if(artist.deathPlace != null && artist.deathPlace !== ""){
            setDied(deathInfo);
        } else {
            setDied(<Fragment></Fragment>);
        }
    }, [artist]);

    useEffect(()=>{
        if(artist.bio.length > 1500){
            showBrief();
        } else {
            showAll();
        }
    }, [artist])

    function showAll(){
        setBio(<Fragment>
            <p>{artist.bio}</p>
            {artist.bio.length > 1500 ?
            (<p onClick={showBrief} className="show-more">Show less</p>):
            (<Fragment></Fragment>)}
        </Fragment>);
    }

    function showBrief(){
        setBio(briefBio);
    }

    return (
        <div>
        {window.screen.width > 600 ? 
        (<div className="info-grid">
                <div className="info-grid-item">
                    <h2>{artist.name}</h2>
                    <p>
                    {parseInt(localStorage.getItem("permission")) > 1 ?
                    (<Link to={`/editartist?artistId=${artist.id}`}>Edit this star's page</Link>) : 
                    (<Fragment></Fragment>)
                    }
                    </p>
                    {bio}
                </div>
                <div>
                    <img className="info-portrait" src={`/rewer/uploads/portraits/${artist.id}.jpg`} alt={`${artist.name}_portrait`} />
                    <p>Born: <i>{`${normalFormat(artist.birthDate.toString())},`}</i></p>
                    <p><i>{artist.birthPlace}</i></p>  
                    {died}
                </div>
            </div>) :
            (<div className="info-grid-item">
                <h2>{artist.name}</h2>
                <p>
                    {parseInt(localStorage.getItem("permission")) > 1 ?
                    (<Link to={`/editartist?artistId=${artist.id}`}>Edit this star's page</Link>) : 
                    (<Fragment></Fragment>)
                    }
                </p>
                <img className="info-portrait" src={`/rewer/uploads/portraits/${artist.id}.jpg`} alt={`${artist.name}_portrait`} />
                <p>Born: <i>{`${normalFormat(artist.birthDate.toString())},`}</i></p>
                <p><i>{artist.birthPlace}</i></p>  
                {died}
                {bio}
            </div>
           )
           }
        </div>
    )
}
