import React, { useState, useEffect, Fragment} from 'react'
import { IGenre, ISeriesGenre } from '../../common/genre';
import { ISeason } from '../../common/season';
import { deleteSeriesGenre, insertSeries, insertSeriesGenre, selectDetailedSeries, updateSeries } from '../callers/seriesCaller';
import GenreAdder from '../components/movie/GenreAdder';
import EditSeasons from '../components/series/EditSeasons';
import EditSeriesDetaills from '../components/series/EditSeriesDetails';
import { useBinder } from '../hooks';
import { changeParam, getParameter } from '../urlManager'

export default function EditSeries() {
    const [series, setSeries, bindSeries] = useBinder({id: parseInt(getParameter("seriesId")), title: "", summary: "", seasons: [], genres: []});
    const [mainMsg, setMainMsg] = useState("");
    useEffect(()=>{
        if(series.id > 0){
            getSeries();
        }
    }, []);
    async function getSeries(){
        let selectedSeries = await selectDetailedSeries(series.id);
        if(selectedSeries){
            setSeries(selectedSeries);
        } else {
            alert("Server error");
        }
    }

    async function createSeries(){
        const newId = await insertSeries(series);
        if(newId){
            setSeries({...series, id: newId});
            changeParam({name: "seriesId", value: newId.toString()});
            setMainMsg("Series successfully created.");
        } else {
            setMainMsg("Internal error: could not create series page.");
        }
    }

    async function saveSeries(){
        if(await updateSeries(series)){
            setMainMsg(`Series details saved: ${new Date().getHours()} : ${new Date().getMinutes()}`);
        } else {
            setMainMsg("Internal error: series details could not be saved.");
        }
    }

    async function addGenre(genre: IGenre){
        let newSeriesGenre: ISeriesGenre = {
            id: 0,
            seriesId: series.id,
            genreId: genre.id,
            genreName: genre.name
        }
        newSeriesGenre.id = await insertSeriesGenre(newSeriesGenre);
        if(newSeriesGenre.id > 0){
            setSeries({...series, genres: [...series.genres, newSeriesGenre]});
        } else {
            alert("Error: could not add genre");
        }
    }

    async function removeGenre(id: number){
        if(await deleteSeriesGenre(id)){
            setSeries({...series, genres: series.genres.filter(s=> s.id !== id)});
        } else {
            alert("Server error: could not delete genre");
        }
    }

    function setSeasons(newSeasons: ISeason[]){
        setSeries({...series, seasons: newSeasons})
    }

    return (
        <div className="main-container">
            {mainMsg}
            <br/>
            {(series.id > 0)?
            (<button onClick={saveSeries}>Save series details</button>):
            (<button onClick={createSeries}>Create series</button>)}
            <EditSeriesDetaills series={series} setSeries={setSeries} bindSeries={bindSeries} />
            {(series.id > 0) ?
            (<Fragment>
                <GenreAdder movie={series} onAdd={(genre: IGenre)=> { addGenre(genre); }} onDelete={(id: number)=> { removeGenre(id) }} />
                <EditSeasons series={series} setSeasons={setSeasons} />
            </Fragment>) :
            (<Fragment></Fragment>)}
        </div>
    )
}