import { IFollow } from "../../common/follow";
import { POSTAsynch } from "./caller";

export async function insertFollow(follow: IFollow): Promise<number | null>{
    const response = await POSTAsynch("/follow/insert_follow", follow);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function deleteFollow(id: number): Promise<boolean>{
    const response = await POSTAsynch("/follow/delete_follow", { id: id });
    if(response.ok){
        return true;
    }
    return false;
}