import React, { useEffect, useState, Fragment } from 'react'
import { useAsyncReference } from '../../hooks';
import UserReviewRow from './UserReviewRow';
import ReviewRowM from './ReviewRowM';
import LoginMsg from '../profile/LoginMsg';
import { selectFollowedReviews } from '../../callers/reviewCaller';

export default function FollowedReviews() {
    const [reviews, setReviews] = useAsyncReference([]); //for the listeners
    const downloadCount = 10;
    const [errorMsg, setErrorMsg] = useState(<Fragment></Fragment>);
    useEffect(()=>{
        if(localStorage.getItem("userName")){
            requestReviews();
        }
        return ()=> {
            removeWindowListener();
        }
    }, []);

    function addWindowListener(){
        window.addEventListener("scroll", windowListener);
    }

    function removeWindowListener(){
        window.removeEventListener("scroll", windowListener);
    }

    function windowListener(){
        if ((window.innerHeight + window.scrollY) >= (document.body.offsetHeight*0.9)) {
            showMore();
        }
    }

    async function showMore(){
        removeWindowListener(); //make sure the listener doenst fire again until we downloaded the new reviews
        let followedReviews = await selectFollowedReviews(downloadCount, reviews.current.length);
        if(followedReviews){
            setReviews([...reviews.current, ...followedReviews]);
            if(followedReviews.length === downloadCount){
                addWindowListener();
            }
        } else {
            alert("Server error: could not download review data.");
        }
    }
    
    async function requestReviews(){
        let followedReviews = await selectFollowedReviews(10, 0);
        if(followedReviews){
            setReviews(followedReviews);
            if(followedReviews.length === downloadCount){
                addWindowListener();
            }
        } else {
            if(localStorage.getItem("userName") != null)  setTimeout(tryRequestReviews, 2000);
        }
    }

    async function tryRequestReviews(){
        let followedReviews = await selectFollowedReviews(downloadCount, 0);
        if(followedReviews){
            setReviews(followedReviews);
            if(followedReviews.length === downloadCount){
                addWindowListener();
            }
        } else {
            setErrorMsg(<LoginMsg message="manage your followers." />);
        }
    }

    return (
        <div>
            {window.screen.width > 600 ? 
            (<Fragment>
                {errorMsg}
                {reviews.current.map(r=> {
                return <UserReviewRow key={`followed-${r.id}`} review={r} />
                })}
                
            </Fragment>) : 
            (<Fragment>
                {errorMsg}
                {reviews.current.map(r=> {
                return <ReviewRowM key={`followed-${r.id}`} review={r} />
                })}
            </Fragment>)
            }
        </div>
    )
}
