import React, { Fragment, useEffect, useState } from 'react'
import { selectMyReview, insertReview, deleteReview, updateReview } from "../../callers/reviewCaller"
import LoginMsg from '../profile/LoginMsg';
import { AddedReview } from './AddedReview';
import { PostReview } from './PostReview';
import { IReview } from "../../../common/review"
import { IMovie } from '../../../common/movie';

interface IMyReview{
    movie: IMovie;
}

export const MyReview: React.FC<IMyReview> = ({movie}: IMyReview) => { 
    const [reviewContent, setReviewContent] = useState(<PostReview review={{id: 0, text: "", movieId: movie.id, date: "", popIndex: 0, score: 5, userName: localStorage.getItem("userName")}} onPost={postReview} />);
    const [mainMsg, setMainMsg] = useState("");
    useEffect(()=>{
        if(localStorage.getItem("userName") == null){
            setReviewContent(<LoginMsg message={"post a review"} />);
        } else {
            if(movie.id > 0){
                getMyReview();
            }
        }
        async function getMyReview(){
            const response = await selectMyReview(movie.id);
            if(response.status == 404){
                setMainMsg("You haven't posted a review for this movie yet.");
                setReviewContent(<PostReview review={{id: 0, text: "", movieId: movie.id, date: "", popIndex: 0, score: 5, userName: ""}} onPost={postReview} />);   
            } else if(response.status === 200){
                let review = await response.json();
                setReviewContent(<AddedReview review={review} onEdit={startEditing} onDelete={removeReview} />);
            } else {
                alert("Server error");
                setMainMsg("Your review info could not bet downloaded from the server.");
            }
        }  
    }, [movie.id]);

    useEffect(()=>{
        if(new Date().getTime() < new Date(movie.releaseDate).getTime()){
            setReviewContent(<p>This movie has not been released yet.</p>);
            setMainMsg("");
        }
    }, [movie]);

    function startEditing(review: IReview){
        setReviewContent(<PostReview review={review} onPost={saveReview} /> );
        setMainMsg("Editing your review:");
    }

    async function saveReview(review: IReview){
        if(await updateReview(review)){
            setReviewContent(<AddedReview review={review} onEdit={startEditing} onDelete={removeReview} />);
            setMainMsg("Review has been updated.");
        }
    }

    async function postReview(review: IReview){
        const newId = await insertReview(review);
        if(newId){
            review = {...review, id: newId, date: new Date().toLocaleDateString("hu-HU"), popIndex: 1};
            setReviewContent(<AddedReview review={review} onEdit={startEditing} onDelete={removeReview} />);
            setMainMsg("Your review has been posted.");
        } else {
            alert("Server error: could not post your review.");
        }
    }

    async function removeReview(review: IReview){
        if(await deleteReview(review.id)){
            setMainMsg("Your review has been deleted.");
            setReviewContent(<PostReview review={{id: 0, text: "", movieId: movie.id, date: "", popIndex: 0, score: 5, userName: ""}} onPost={postReview} /> );
        } else {
            alert("Server error: could not delete your review.");
        }
    }

    return (
        <Fragment>
            <h3 className="center-text">My Review</h3>
            <p>{mainMsg}</p>
            <div className="review-container">
                {reviewContent}
            </div>
        </Fragment>
    )
}
