import { IReview, IUserReview, WSelectReviewsByMovieRequest } from "../../common/review";
import { IReviewLike } from "../../common/reviewLike";
import { POSTAsynch } from "./caller";
export enum EReviewOrderBy{
    popIndex = "popIndex",
    date = "date"
}

export async function selectMyReview(movieId: number): Promise<Response>{
    const response = await POSTAsynch("/review/select_review_by_user_and_movie", {userName: localStorage.getItem("userName"), movieId: movieId, limit: 1, offset: 0});
    return response;
}

export async function selectReviewsByMovie(request: WSelectReviewsByMovieRequest): Promise<IReview[] | null>{
    const response = await POSTAsynch("/review/select_reviews_by_movie", request);
  //  console.log(await response.json())
    if(response.status === 200){
        return await response.json();
    }
    return null;
}

export async function selectFollowedReviews(limit: number, offset: number): Promise<IReview[] | null>{
    const response = await POSTAsynch("/review/select_followed_reviews", { limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function selectReviewsByUser(userName: string, limit: number, offset: number, orderby: EReviewOrderBy): Promise<IUserReview[] | null>{
    const response = await POSTAsynch("/review/select_reviews_by_user", { userName: userName, limit: limit, offset: offset, orderby: orderby });
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function insertReview(review: IReview): Promise<number | null>{
    const response = await POSTAsynch("/review/insert_review", review);
    if(response.status === 200){
        return (await response.json()).newId;
    }
    return null;
}

export async function deleteReview(id: number): Promise<boolean>{
    const response = await POSTAsynch("/review/delete_review", { id: id });
    if(response.status === 200){
        return true;
    }
    return false;
}

export async function updateReview(review: IReview): Promise<boolean>{
    const response = await POSTAsynch("/review/update_review", review);
    if(response.status === 200){
        return true;
    }
    return false;
}

export async function insertReviewLike(reviewLike: IReviewLike): Promise<number | null>{
    const response = await POSTAsynch("/review/insert_reviewlike", reviewLike);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function updateReviewLike(reviewLike: IReviewLike): Promise<boolean>{
    const response = await POSTAsynch("/review/update_reviewlike", reviewLike);
    if(response.ok){
        return true;
    }
    return true;
}
