import React, { useState, useEffect} from 'react'
import { Link } from "react-router-dom"
import { IUser } from '../../../common/user';

interface IFollowedUserRow{
    user: IUser;
}

export const FollowedUserRow: React.FC<IFollowedUserRow> = ({user}: IFollowedUserRow) => {
    const [imgSrc, setImgsrc] = useState("");
    useEffect(()=>{
        setImgsrc(`/rewer/uploads/avatars/${user.userName}.jpg`);
    }, [user])
    return (
        <Link to={`/user?userName=${user.userName}`}>
            <div className="followed-user-row highlight">
                <img src={imgSrc} onError={()=> { setImgsrc("/rewer/uploads/avatars/default.jpg") }} className="followed-user-row-item" alt="user-poster" />
                <p className="followed-user-row-item">{user.nickName}</p>
            </div>
        </Link>            
    )
}
export default FollowedUserRow;