import React, { useState, useEffect, Fragment} from 'react'
import { selectMoviesByScore } from '../../callers/movieCaller';
import { MovieRow } from './MovieRow'

export default function TopRatedSides() {
    const[topRateds, setTopRateds] = useState([]);
    useEffect(()=>{
        getTopRateds();
    }, []);

    async function getTopRateds(){
        let tops = await selectMoviesByScore(3, 4, 0);
        if(tops){
            setTopRateds(tops);
        }
    }
    return (
        <Fragment>
             <h3 className="center-text">Top Rated</h3>      
                    {topRateds.map(t=>{
                        return <MovieRow key={`top-rated-${t.id}`} movie={{...t, summary: ""}} />
                    })} 
        </Fragment>
    )
}
