import React, { useEffect} from 'react';
import './App.css';
import "./main.css";
// @ts-ignore
import { Route, BrowserRouter as Router, Switch} from "react-router-dom";
import Home from "./pages/Home";
import EditMovie from "./pages/EditMovie";
import Profile from "./pages/Profile";
import Helmet from "react-helmet";
import { Header } from './components/header/Header';
import { handleLogin } from './callers/authUserCaller';
import EditArtist from './pages/EditArtist';
import Movie from './pages/Movie';
import Star from './pages/Star';
import User from './components/profile/User';
import FollowsPage from './pages/FollowsPage';
import EditSeries from './pages/EditSeries';
import Series from './pages/Series';
import SearchPage from './pages/SearchPage';
import { Redirect, useParams } from 'react-router';
import { getParameter } from './urlManager';
function App() {
  useEffect(()=>{
    log();
  }, []);
  async function log(){
    if(await handleLogin()){
      console.log("user is logged in");
    } else {
      console.log("user is not logged in");
    } 
  } // <Route exact path="/movie" component={Movie}  />
  return (
    <>
    <Router>
    <Helmet>
      <title>Rewer</title>
      <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    </Helmet>
    <Header />
    <div className="page"> 
      <Switch>
          <Redirect from="/link_movie:movieId" to="/movie" />
          <Route exact path="/rewer" component={Home} />
          <Route exact path="/movie" render={(props) => <Movie {...props}  key={new Date().getTime()}/>} />
          <Route exact path="/series" render={(props) => <Series {...props}  key={new Date().getTime()}/>} />
          <Route exact path="/profile" component={Profile} />
          <Route exact path="/user" component={User} />
          <Route exact path="/editartist" component={EditArtist} />
          <Route exact path="/star" component={Star} />
          <Route exact path="/editmovie" component={EditMovie} />
          <Route exact path="/editseries" component={EditSeries} />
          <Route exact path="/follows" component={FollowsPage} />
          <Route exact path="/search" component={SearchPage} />
          <Route exact path="/home" component={Home} />
      </Switch>
      </div>
    </Router>
    
    </>
  );
}

export default App;
