import React, { Fragment, useState, useEffect } from 'react'
import { Link } from "react-router-dom"
import { selectLatestMovies } from '../../callers/movieCaller';
import MovieDiv from './MovieDiv';

export default function FeaturedSides() {
    const[featuredMovies, setFeaturedMovies] = useState([]);
    useEffect(()=>{
        getLatestFeatures();
    }, []);

    async function getLatestFeatures(){
        const index = Math.floor(Math.random()*5);
        let movies = await selectLatestMovies(2, index);
        if(movies){
            setFeaturedMovies(movies);
        }
    }

    return (
        <Fragment>
            <h3 className="center-text">Featured Films</h3>
                    <div className="double-grid">
                        {featuredMovies.map(f=> {
                            return (<Link key={`featured-${f.id}`} to={`/movie?movieId=${f.id}`}>
                                        <MovieDiv movie={f} />
                                    </Link>)
                        })}
                    </div>  
        </Fragment>
    )
}
