import React, { useState, useEffect, Fragment } from 'react'
import { selectFollowers } from '../../callers/userCaller'
import FollowedUserRow from './FollowedUserRow';
import { Link } from "react-router-dom";

interface IFollowingUsers{
    length: number;
}

export const FollowingUsers: React.FC<IFollowingUsers> = ({length}: IFollowingUsers) => {
    const [users, setUsers] = useState([]);
    useEffect(()=>{
        if(localStorage.getItem("userName")){
            getFollowingUsers();
        }
    }, []);

    async function getFollowingUsers(){
        let following = await selectFollowers({userName: localStorage.getItem("userName"), limit: length});
        if(following){
            setUsers(following);
        }
    }

    return (
        <Fragment>
            <h3 className="center-text">My Followers</h3>
            {users.slice(0, 8).map(u=>{
                return <FollowedUserRow key={`followed-${u.userName}`} user={u} />
            })}
            {users.length > length ?
            (<Link to={"TODO"}>More</Link>):
            (<Fragment></Fragment>)}
        </Fragment>
    )
}
export default FollowingUsers;