import { POSTAsynch, UploadFile } from "../callers/caller";
import { IArtist, IDetailedArtist } from "../../common/artist";

export async function selectArtistsByKeyword(keyword: string, limit: number, offset: number): Promise<IArtist[]>{
    const response = await POSTAsynch("/artist/select_artists_by_keyword", { keyword: keyword, limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function selectDetailedArtist(id: number): Promise<IDetailedArtist | null>{
    const response = await POSTAsynch("/artist/select_detailed_artist", { id: id });
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function insertArtist(artist: IArtist): Promise<number | null>{
    const response = await POSTAsynch("/artist/insert_artist", artist);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function updateArtist(artist: IArtist): Promise<boolean>{
    const response = await POSTAsynch("/artist/update_artist", artist);
    if(response.ok){
        return true
    }
    return false;
}

export async function uploadPortrait(file: FormData): Promise<boolean>{
    const response = await UploadFile("/artist/upload_portrait", file);
    if(response.ok){
        return true;
    }
    return false;
}