import React, { Fragment, useEffect, useState } from 'react'
import { normalFormat } from '../../dateParser';
import { useAsyncReference } from '../../hooks';
import { EReviewOrderBy, selectReviewsByMovie } from "../../callers/reviewCaller";
import ReviewList from './ReviewList';
import ReviewOrderBySelect from './ReviewOrderBySelect';
import { IMovie } from '../../../common/movie';


interface IMovieReviewFeed{
    movie: IMovie;
}

export const MovieReviewFeed: React.FC<IMovieReviewFeed> = ({movie}: IMovieReviewFeed) => {
    const [reviews, setReviews] = useAsyncReference([]);
    const [orderBy, setOrderBy] = useState(undefined);
    const [reviewHeader, setReviewHeader] = useState("");
    const reviewPerPage = 5;
    useEffect(()=>{
        return ()=> {
            removeWindowListener();
        }
    }, []);
    useEffect(()=>{
        if(new Date().getTime() >= new Date(movie.releaseDate).getTime()){
            if(orderBy === EReviewOrderBy.popIndex){
                if(movie.id > 0) getReviews();
            } else {
                setOrderBy(EReviewOrderBy.popIndex);
            }          
        } else {
            setReviewHeader(`Reviews will be available on ${normalFormat(movie.releaseDate.toString())}`);
        } 
        return ()=> {
            removeWindowListener();
        }
    }, [movie.id, movie.releaseDate]);

    useEffect(()=>{
        if(movie.id > 0 && orderBy != null){
            getReviews();
        }
        return ()=> {
            removeWindowListener();
        }
    }, [orderBy]);

    async function getReviews(){
        let requestedReviews = await selectReviewsByMovie({ movieId: movie.id, limit: reviewPerPage, offset: 0, orderby: orderBy});
        if(requestedReviews){
            setReviews(requestedReviews);
            setReviewHeader("Reviews");
            if(requestedReviews.length === reviewPerPage) addWindowListener();                
        } else {
            setReviewHeader("Server errror: could not download reviewdata.");
        }
    }

    async function getMoreReviews(){
        removeWindowListener();
        let requestedReviews = await selectReviewsByMovie({ movieId: movie.id, limit: reviewPerPage, offset: reviews.current.length, orderby: orderBy});
        if(requestedReviews){
            setReviews([...reviews.current, ...requestedReviews]);
            if(requestedReviews.length === reviewPerPage) addWindowListener();
        } else {
            alert("Server errror: could not download reviewdata.");
        }
    }

    function addWindowListener(){
        window.addEventListener("scroll", windowListener);
    }

    function removeWindowListener(){
        window.removeEventListener("scroll", windowListener);
    }

    function windowListener(){
        if ((window.innerHeight + window.scrollY) >= (document.body.offsetHeight*0.9)) {
            getMoreReviews();
        }
    }

    return (
        <div>
            <h2 className="center-text">{reviewHeader}</h2>
            {new Date().getTime() >= new Date(movie.releaseDate).getTime() ? 
            (<Fragment>
                <ReviewOrderBySelect defaultValue={orderBy} onUpdate={(newValue: EReviewOrderBy)=> { setOrderBy(newValue); }} />
                <ReviewList reviews={reviews.current} />
            </Fragment>): 
            (<Fragment></Fragment>)}    
        </div>
    )
}
export default MovieReviewFeed;