import React, { useState, useEffect } from 'react'
import { insertArtist, selectDetailedArtist, updateArtist } from '../callers/artistCaller'
import EditDetails from '../components/artist/EditDetails'
import { useBinder } from '../hooks'
import { changeParam, getParameter } from '../urlManager'

export default function EditArtist() {
    const [artist, setArtist, bindArtist] = useBinder({id: parseInt(getParameter("artistId")), name: "", birthPlace: "", birthDate: new Date(), deathPlace: "", deathDate: new Date(), bio: ""})
    const [mainMsg, setMainMsg] = useState("");

    useEffect(()=>{
        if(artist.id > 0){
            getArtist();
        }
    }, []);

    async function getArtist(){
        const selectedArtist = await selectDetailedArtist(artist.id);
        if(selectedArtist){
            setArtist(selectedArtist);
        } else {
            alert("Server error: could not download artist data.");
        }
    }

    async function createArtist(){
        const newId = await insertArtist(artist);
        if(newId){
            setMainMsg("Artist's page successfully created.");
            changeParam({name: "artistId", value: newId.toString()});
            setArtist({...artist, id: newId});
        } else{
            setMainMsg("Internal error: the server was unable to fulfill your request.");
        } 
    }

    async function saveArtist(){
        if(await updateArtist(artist)){
            setMainMsg(`Changes saved: ${new Date().getHours()} : ${new Date().getMinutes()}`);
        } else {
            setMainMsg("WARNING: your last attempt to save your work was declined by the server.");
            alert("Request failed: server error.");
        }        
    }

    return (
        <div className="main-container">
            {mainMsg}
            <br/>
            {(artist.id > 0)? 
            (<button onClick={saveArtist}>Save changes</button>): 
            (<button onClick={createArtist}>Create artist's page</button>)}
            <EditDetails artist={artist} setArtist={setArtist} bindArtist={bindArtist} />
        </div>
    )
}