export class ErrorMessenger{
    messages: IMessage[];
    defaultError: string;
    constructor(messages: IMessage[], defaultError?: string){
        this.messages = messages;
        if(defaultError) {
            this.defaultError = defaultError;
        } else {
            this.defaultError = "Unknown error";
        }
    }
    getError(statusCode: number): string{
        let result = this.messages.find(m=> m.statusCode === statusCode);
        if(result) return result.message;
        return this.defaultError;
    }

}

export interface IMessage{
    statusCode: number;
    message: string;
}