import React, { Fragment, useEffect, useState } from 'react'
import SeriesSlider from './SeriesSlider';
import {Link} from "react-router-dom"
import SeriesRow from './SeriesRow';
import { selectLatestPreviewSeries } from '../../callers/seriesCaller';
import { IPreviewSeries } from '../../../common/series';

export default function LatestSeries() {
    const [series, setSeries] = useState([]);
    const [selectedSeries, setSelectedSeries] = useState({id: 0, title: "", summary: "", lastEpisodeId: 0, lastEpisodeTitle: ""});
    useEffect(()=>{
        getSeries();
    }, []);
    async function getSeries(){
        let latestSeries = await selectLatestPreviewSeries(6, 0);
        if(series){
            setSeries(latestSeries);
            setSelectedSeries(latestSeries[0]);
        } else {
            //TODO
        }
    }
    return (
        <div>
            {window.screen.width > 800 ?
            (<div className="highlight-grid">
                <SeriesSlider series={series} onHover={(s: IPreviewSeries)=> { setSelectedSeries(s) }} />
                <div className="highlighted-movie fade">
                    <h3>{selectedSeries.title}</h3>
                    <p className="highlight-text">{selectedSeries.summary}</p>
                    <p>Last episode: </p>
                    <Link to={`/movie?movieId=${selectedSeries.lastEpisodeId}`}>{selectedSeries.lastEpisodeTitle}</Link><br/><br/>
                    <Link to={`/movie?movieId=${selectedSeries.id}`}>Read more</Link>
                </div>
            </div> ) : 
            (<Fragment>{series.map(s=> {
                return <SeriesRow key={`series-row${s.id}`} series={s} />
            })}</Fragment>)}
           
        </div>
    )
}
