export const itemReplacer = (array, oldItem, newItem) => {
  array.map(item=> item === oldItem? newItem : item);
}

export const replacedList = (array, oldItem, newItem) => {
  return array.map(item=> item === oldItem? newItem : item);
}

export async function resizeImageFn(file, maxWidth: number, minWidth: number, quality = 1) {
  const Compress = require('compress.js')
  const compress = new Compress()
  const resizedImage = await compress.compress([file], {
    size: 1, // the max size in MB, defaults to 2MB
    quality: quality, // the quality of the image, max is 1,
    maxWidth: maxWidth, // the max width of the output image, defaults to 1920px
    maxHeight: minWidth, // the max height of the output image, defaults to 1920px
    resize: true // defaults to true, set false if you do not want to resize the image width and height
  })
  const img = resizedImage[0];
  const base64str = img.data
  const imgExt = img.ext
  const resizedFiile = Compress.convertBase64ToFile(base64str, imgExt)
  return resizedFiile;
}