import React, { useState, useEffect } from 'react'
import { Link } from "react-router-dom"
import { Score } from './Score';
import { normalFormat } from '../../dateParser';
import { IMovie } from '../../../common/movie';

interface IMovieRow{
    movie: IMovie;
}

export const MovieRow: React.FC<IMovieRow> = ({movie}: IMovieRow) => {
    const [imgsrc, setImgsrc] = useState(`/rewer/uploads/posters/${movie.id}.jpg`);
    const [imgClass, setImgClass] = useState("");
    const [summary, setSummary] = useState("");
    useEffect(()=>{
        if(movie.avgScore > 6){
            setImgClass("high-poster");
        } else if(movie.avgScore > 3){
            setImgClass("medium-poster");
        } else if(movie.avgScore > 0) {
            setImgClass("low-poster");
        } else {
            setImgClass("");
        }
        if(window.screen.width > 600){
            if(movie.summary.length > 120){
                setSummary(`${movie.summary.substring(0, 120)}...`);
            } else {
                setSummary(movie.summary);
            }
        }
    }, [movie]);
    return (
        <Link className="movie-row-grid" to={`/movie?movieId=${movie.id}`}>
            <img className={imgClass} src={imgsrc} alt={`poster-${movie.id}`} title={movie.title} onError={()=>{ setImgsrc(`/rewer/uploads/posters/0.jpg`) }} />
            <div>
                <p className="row-title">{movie.title}</p>
                <p className="summary">{summary}</p>
            </div>
            {(new Date(movie.releaseDate) >= new Date()) ? 
            (<Score value={movie.avgScore} />) :
            (<p className="movie-row-date">{normalFormat(movie.releaseDate.toString())}</p>)
            }
        </Link>
    )
}
