import React, { useState, useEffect, Fragment} from 'react'
import { Link } from "react-router-dom"
import { ICast } from '../../../common/cast';

interface ISpotlightRow{
    cast: ICast;
}

export const SpotlightRow: React.FC<ISpotlightRow> = ({cast}: ISpotlightRow) => {
    const [imgSrc, setImgSrc] = useState("");
    useEffect(()=>{
        setImgSrc(`/rewer/uploads/portraits/${cast.artistId}.jpg`);
    }, []);
    return (
        <div className="cast-row">
            <img src={imgSrc} onError={()=>{ setImgSrc(`/rewer/uploads/portraits/0.jpg`); }} alt={`star-${cast.artistName}`} />
            <Link to={`/star?artistId=${cast.artistId}`} className="line-start" >{cast.artistName}: </Link>
            {window.screen.width >= 1280 ?
            ( <Link to={`/movie?movieId=${cast.movieId}`} className="line-end" >{cast.movieName}</Link>):
            (<Fragment></Fragment>)}      
        </div>
    )
}

export default SpotlightRow;