import React, { useState, useEffect, Fragment } from 'react'
import { MovieRowList } from './MovieRowList';
import MovieSlider from './MovieSlider'
import { Link } from "react-router-dom"
import "./movieStyle.css"
import { selectLatestMovies } from '../../callers/movieCaller';
import { IDetailedMovie } from '../../../common/movie';
import { ICast } from '../../../common/cast';

interface ILatestMovies{
    onMovieUpdate?: (movies: ICast[]) => void;
}

export const LatestMovies: React.FC<ILatestMovies> = ({onMovieUpdate}: ILatestMovies) => {
    const [movies, setMovies] = useState([]);
    const [selectedMovie, setSelectedMovie] = useState({id: 0, title: "", summary: "", casts: [], genres: [], releaseDate: ""});
    useEffect(()=>{
        requestMovies();
    }, []);
    async function requestMovies(){
        let latests = await selectLatestMovies(9, 0);
        if(latests){
            setMovies(latests);
            setSelectedMovie(latests[0]);
        } else {
            //TODO goto 404
        }
    }
    useEffect(()=>{
        if(onMovieUpdate){    
            onMovieUpdate(getAllCasts());
        }
    }, [movies]);


    function getAllCasts(){
        let allCasts: ICast[] = [];
        for(let movie of movies) {
            allCasts = [...allCasts, ...movie.casts];
        }
        return allCasts;
    }

    return (
        <div>
            {window.screen.width > 600 ? 
            (<Fragment>

                <div className="highlight-grid">
                    <MovieSlider movies={movies} onHover={(movie: IDetailedMovie)=> { setSelectedMovie(movie) }} />
                    <div className="highlighted-movie fade">
                        <h3>{selectedMovie.title}</h3>
                        <p className="highlight-text">{selectedMovie.summary}</p>
                        <p>Directed by:</p>
                        <ul>
                            {selectedMovie.casts.filter(c=> c.castTypeId === 1).map(d=>{
                                return <li key={`latest-${d.artistId}`}><Link to={`/star?artistId=${d.artistId}`}>{d.artistName}</Link></li>
                            })}
                        </ul>
                        <p>Starring: </p>
                        <ul>
                            {selectedMovie.casts.filter(c=> c.castTypeId > 3).slice(0, 3).map(d=>{
                                return <li key={`latest-${d.artistId}`}><Link to={`/star?artistId=${d.artistId}`}>{d.artistName}</Link></li>
                            })}
                        </ul>
                        <Link to={`/movie?movieId=${selectedMovie.id}`}>Read more</Link>
                    </div>
                </div> 
            </Fragment>) :
            (<MovieRowList movies={movies} />)
            }       
        </div>
    )
}

export default LatestMovies;