import React, { useState, useEffect, Fragment} from 'react'
import { useAsyncReference } from '../../hooks';
import { getParameter } from '../../urlManager';
import UserReviewRow from "../review/UserReviewRow";
import ReviewOrderBySelect from '../review/ReviewOrderBySelect';
import UserInfo from './UserInfo';
import ReviewRowM from '../review/ReviewRowM';
import SearchSide from '../SearchSide';
import FeaturedSeriesSides from '../series/FeaturedSeriesSides';
import FeaturedSides from '../movie/FeaturedSides';
import { selectDetailedUser } from '../../callers/userCaller';
import { EReviewOrderBy, selectReviewsByUser } from '../../callers/reviewCaller';
import { IUserReview } from '../../../common/review';

export default function User() {
    const [user, setUser] = useState({userName: getParameter("userName"), nickName: "", permission: 1, about: ""});
    const [reviews, setReviews] = useAsyncReference([]);
    const reviewPerPage = 5;
    const [orderBy, setOrderBy] = useState(EReviewOrderBy.date);
    useEffect(()=>{
        getUserInfo();
        return ()=>{
            removeWindowListener();
        }
    }, []);

    useEffect(()=>{
        requestReviews();
        return ()=>{
            removeWindowListener();
        }
    }, [orderBy]);

    async function getUserInfo(){
        let userInfo = await selectDetailedUser(getParameter("userName"));
        console.log(userInfo);
        if(userInfo){
            setUser(userInfo);
        } else {
            //TODO
        }
    }

    function addWindowListener(){
        window.addEventListener("scroll", windowListener);
    }

    function removeWindowListener(){
        window.removeEventListener("scroll", windowListener);
    }

    var windowListener = function() {
        if ((window.innerHeight + window.scrollY) >= (document.body.offsetHeight*0.9)) {
            window.removeEventListener("scroll", windowListener);
            getMoreReviews();
        }
    } 

    async function requestReviews(){
        removeWindowListener();
        let userReviews: IUserReview[] = await selectReviewsByUser(user.userName, reviewPerPage, 0, orderBy);
        if(userReviews){
            setReviews(userReviews);
            if(userReviews.length === reviewPerPage) {} addWindowListener();
        } 
    }

    async function getMoreReviews(){
        removeWindowListener(); 
        let requestedReviews = await selectReviewsByUser(user.userName, reviewPerPage, reviews.current.length, orderBy);
        if(requestedReviews){
            setReviews([...reviews.current, ...requestedReviews]);
            if(requestedReviews.length === reviewPerPage) {} //addWindowListener();
        } else {
            alert("Server errror: could not download reviewdata.");
        } 
    }

    return (
        <Fragment>
            {window.screen.width > 800 ?
            (<div className="main-grid">
                <div className="left-item">
                    <div className="side-container medium-side">
                        <FeaturedSides />
                    </div>
                </div>
                <div className="main-item">
                    <UserInfo user={user} />
                    <br/>
                    <h2>{user.nickName}'s reviews</h2>
                    <ReviewOrderBySelect defaultValue={orderBy} onUpdate={(newValue: EReviewOrderBy)=> { setOrderBy(newValue); }} />
                    {window.screen.width > 600 ? 
                    (<Fragment>{reviews.current.map(r=> {
                        return <UserReviewRow key={`review-${r.id}`} review={{...r, nickName: ""}} />
                    })}</Fragment>) :
                    (<Fragment>{reviews.current.map(r=>{
                        return <ReviewRowM key={`review-${r.id}`} review={{...r, nickName: ""}} />
                    })}</Fragment>)
                    }
                </div>
                <div className="right-item">
                    <div className="side-container medium-side">
                        <SearchSide />
                    </div>
                    <div className="side-container medium-side">
                        <FeaturedSeriesSides />
                    </div>
                </div>
            </div>):
            (<div className="main-container">
                <UserInfo user={user} />
                <br/>
                <h2>{user.nickName}'s reviews</h2>
                <ReviewOrderBySelect defaultValue={orderBy} onUpdate={(newValue: EReviewOrderBy)=> { setOrderBy(newValue); }} />
                {window.screen.width > 600 ? 
                (<Fragment>{reviews.current.map(r=> {
                    return <UserReviewRow key={`review-${r.id}`} review={{...r, nickName: ""}} />
                })}</Fragment>) :
                (<Fragment>{reviews.current.map(r=>{
                    return <ReviewRowM key={`review-${r.id}`} review={{...r, nickName: ""}} />
                })}</Fragment>)
                }
            </div>)}
        </Fragment>
        
    )
}
