import { ISeason } from "../../common/season";
import { POSTAsynch } from "./caller";

export async function insertSeason(season: ISeason): Promise<number | null>{
    const response = await POSTAsynch("/season/insert_season", season);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function deleteSeason(id: number): Promise<boolean>{
    const response = await POSTAsynch("/season/delete_season", { id: id });
    if(response.ok){
        return true;
    }
    return false;
}