import React, { useState, useEffect } from 'react'
import { GenreSelect } from './GenreSelect';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { getGenres } from '../../callers/movieCaller';
import { IGenre } from '../../../common/genre';

interface IGenreAdder{
    movie: any;
    onAdd: (genre: IGenre) => void;
    onDelete: (id: number) => void;
}
export const GenreAdder: React.FC<IGenreAdder> = ({movie, onAdd, onDelete}: IGenreAdder)=> {
    const [genreList, setGenreList] = useState([]);
    const [selectedGenre, setSelectedGenre] = useState({id: 0, name: ""});
    useEffect(()=>{
        requestGenres();
    },[]);

    async function requestGenres(){
        let genres = await getGenres();
        if(genres != null){
            setGenreList(genres);
        } else {
            alert("Server error: could not download the list of genres");
        }
    }

    function addGenre(){
        if(selectedGenre.id > 0){
            onAdd(selectedGenre);
        } else {
            onAdd(genreList[0]);
        }
    }

    return (
        <div>
            <h3>Genres:</h3>
            <div className= "edit-genre-container">
            {movie.genres.map(g=> {
                return <div key={`genre-${g.id}`} className="edit-genre"><span>{g.genreName}</span><button onClick={()=> { onDelete(g.id); }} ><FontAwesomeIcon icon={faTimes} /></button></div>
            })}
            </div>          
            <GenreSelect genres={genreList} selectedGenre={selectedGenre} setSelectedGenre={(g: IGenre)=> { setSelectedGenre(g) }} />
            <button onClick={()=>{ addGenre() }}>Add</button><br/>
        </div>
    )
}
export default GenreAdder;