import { IGenre, IMovieGenre, ISeriesGenre } from "../../common/genre";
import { IDetailedMovie, IMovie } from "../../common/movie";
import { POSTAsynch, GETAsynch, UploadFile } from "../callers/caller";

export async function selectDetailedMovie(id: number): Promise<IDetailedMovie | null>{
    const response = await POSTAsynch("/movie/select_detailed_movie", { id: id});
    if(response.status !== 200){
        return null;
    }
    return await response.json();
}

export async function selectLatestMovies(limit: number, offset: number): Promise<IDetailedMovie[] | null>{
    const response = await POSTAsynch("/movie/select_latest_movies", {limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function selectLatestMoviesByGenre(genreId: number, limit: number, offset: number): Promise<IDetailedMovie[] | null>{
    const response = await POSTAsynch("/movie/select_latest_movies_by_genre", { genreId: genreId, limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function selectMoviesByScore(min: number, limit: number, offset: number): Promise<IDetailedMovie[] | null>{
    const response = await POSTAsynch("/movie/select_movies_by_score", { min: min, limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function getGenres(): Promise<IGenre[] | null>{
    const response = await GETAsynch("/genre/select_genres");
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function insertMovie(movie: IMovie): Promise<number | null>{
    const response = await POSTAsynch("/movie/insert_movie", movie);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function updateMovie(movie: IMovie): Promise<boolean>{
    const reponse = await await POSTAsynch("/movie/update_movie", movie);
    if(reponse.ok){
        return true;
    }
    return false;
}

export async function uploadPoster(file: FormData): Promise<boolean>{
    const response = await UploadFile("/movie/upload_poster", file);
    if(response.ok){
        return true;
    }
    return false;
}

export async function insertMovieGenre(movieGenre: IMovieGenre): Promise<number | null >{
    const response = await POSTAsynch("/genre/insert_movie_genre", movieGenre);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function deleteMovieGenre(id: number): Promise<boolean>{
    const response = await POSTAsynch("/genre/delete_movie_genre", { id: id });
    if(response.ok){
        return true;
    }
    return false;
}

export async function selectUpcomingMovies(limit: number, offset: number): Promise<IDetailedMovie[] | null>{
    const response = await POSTAsynch("/movie/select_upcoming_movies", { limit: limit, offset: offset});
    if(response.ok){
        return await response.json();
    }
    return null;
}

export function compareGenresByName(genre1: IGenre, genre2: IGenre){
    return genre1.name.localeCompare(genre2.name);
}

export function compareMovieGenresByName(genre1: IMovieGenre | ISeriesGenre, genre2: IMovieGenre | ISeriesGenre){
    return genre1.genreName.localeCompare(genre2.genreName);
}