import { ICast, ICastType, IMovieCast, ISeriesCast } from "../../common/cast";
import { GETAsynch, POSTAsynch } from "./caller";

export async function getCastTypes(): Promise<ICastType[] | null >{
    const response = await GETAsynch("/cast/select_cast_types");
    if(response.ok){
        return await response.json();
    }
    return null;
}

export async function insertCast(cast: ICast): Promise<number | null>{
    const response = await POSTAsynch("/cast/insert_cast", cast);
    if(response.ok){
        return (await response.json()).newId;
    }
    return null;
}

export async function insertCasts(casts: ICast[]): Promise<ICast[] | null>{
    const response = await POSTAsynch("/cast/insert_casts", { casts: casts });
    if(response.ok){
        return (await response.json());
    }
    return null;
}

export async function updateCast(cast: ICast): Promise<boolean>{
    const response = await POSTAsynch("/cast/update_cast", cast);
    if(response.ok){
        return true;
    }
    return false;
}

export async function deleteCast(id: number): Promise<boolean>{
    const response = await POSTAsynch("/cast/delete_cast", { id: id});
    if(response.ok){
        return true;
    }
    return false;;
}


export function compareCastType(cast1: ICast, cast2: ICast){
    if(cast1.castTypeId < cast2.castTypeId){
        return -1;
    }
    if(cast1.castTypeId > cast2.castTypeId){
        return 1;
    }
    return 0;
}

export function compareDate(cast1: IMovieCast, cast2: IMovieCast){
    if(new Date(cast1.movieDate) < new Date(cast2.movieDate)){
        return -1;
    }
    if(new Date(cast1.movieDate) > new Date(cast2.movieDate)){
        return 1;
    }
    return 0;
}